@charset "UTF-8";
body {
  margin: 0;
  padding: 0;
  min-width: 280px;
  font-family: "Neue-regular", "Arial", sans-serif;
  color: #5F616D;
  font-size: 13px;
  line-height: 20px;
  font-weight: 400;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

input,
textarea,
button {
  font: inherit;
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "Neue-medium";
  src: url("../fonts/Neue-Medium.woff2") format("woff2"), url("../fonts/Neue-Medium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Neue-semibold";
  src: url("../fonts/Neue-DemiBold.woff2") format("woff2"), url("../fonts/Neue-DemiBold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Neue-bold";
  src: url("../fonts/Neue-Bold.woff2") format("woff2"), url("../fonts/Neue-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Neue-regular";
  src: url("../fonts/Neue-Regular.woff2") format("woff2"), url("../fonts/Neue-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
    width: 94%;
    max-width: 1340px;
  }
}
@media (min-width: 1140px) {
  .container {
    width: 1340px;
  }
}
.filters {
  display: flex;
  justify-content: space-between;
}

.filters__form {
  width: 22.7%;
  background-color: #F0F0F0;
}

.filters__categories {
  margin-left: 55px;
  width: 77.3%;
}

.tabs {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 39px;
  padding-top: 20px;
  padding-bottom: 20px;
  border: 1px solid #EFEFEF;
  border-radius: 18px;
}

.tabs__sorting-wrap {
  display: flex;
  justify-content: space-between;
}

.tabs__sorting {
  font-family: "Neue-semibold";
  color: #22242A;
  padding-left: 30px;
  margin-right: 80px;
  font-size: 15px;
  line-height: 21px;
}

.tabs__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
}

.tabs__item {
  margin-right: 57px;
}
.tabs__item:last-child {
  margin-right: 0;
}

.tabs__link {
  color: #9A9DAB;
}
.tabs__link:hover, .tabs__link:active {
  color: #22242A;
}

.tabs__sorting-view {
  padding-right: 20px;
}

.tabs button {
  padding: 0;
  border: 0;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  width: 24px;
  height: 24px;
}

.tabs__sorting-tile {
  background-image: url("../img/group-table.svg");
  background-repeat: no-repeat;
  margin-right: 2px;
}

.wares--sorting-tile .tabs__sorting-tile {
  background-image: url("../img/group-table-active.svg");
}

.tabs__sorting-simple {
  background-image: url("../img/group-list.svg");
  background-repeat: no-repeat;
  margin-right: 27px;
}

.wares--sorting-simple .tabs__sorting-simple {
  background-image: url("../img/group-list-active.svg");
}

.wares--sorting-tile .wares__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.wares--sorting-simple .wares__list {
  display: block;
}

.wares__item {
  position: relative;
  margin-bottom: 35px;
}

.wares--sorting-tile .wares__item {
  position: relative;
  width: 31%;
  margin-right: 3.5%;
}
.wares--sorting-tile .wares__item:nth-child(3n) {
  margin-right: 0;
}

.wares--sorting-simple .wares__item {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 26px;
  align-items: center;
}

.wares--sorting-simple .wares__item--choose {
  position: relative;
  display: flex;
  width: 48%;
  align-items: flex-end;
  margin-bottom: 20px;
  padding-right: 70px;
  padding-right: 25px;
  padding-left: 35px;
  box-sizing: border-box;
  border-radius: 18px;
}
.wares--sorting-simple .wares__item--choose .wares__features-wrap {
  padding-left: 0;
  padding-bottom: 5px;
  margin-bottom: 0;
  order: 1;
  flex-direction: column;
  align-items: flex-start;
}
.wares--sorting-simple .wares__item--choose .wares__fig {
  order: 2;
  width: 60%;
  margin-top: 0;
  height: 300px;
}
.wares--sorting-simple .wares__item--choose .wares__fig img {
  width: 220px;
  height: 300px;
}
.wares--sorting-simple .wares__item--choose .wares__sale {
  top: 40px;
  bottom: auto;
}
.wares--sorting-simple .wares__item--choose .wares__order {
  padding-top: 0;
  margin-bottom: 36px;
}
.wares--sorting-simple .wares__item--choose .wares__figcaption {
  padding-bottom: 0;
}
.wares--sorting-simple .wares__item--choose .wares__price {
  margin-bottom: 11px;
}

.wares__item--choose {
  background: linear-gradient(90deg, #F3F5F9 0%, #ECF1FC 100%);
  padding-left: 40px;
  box-sizing: border-box;
  border-radius: 18px;
}
.wares__item--choose .wares__sale {
  position: absolute;
  top: 40px;
  left: 42px;
  width: 62px;
  height: 30px;
  font-size: 14px;
  line-height: 30px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  background: #22242A;
  border-radius: 6px;
}
.wares__item--choose .wares__figcaption {
  padding-bottom: 0;
  margin-bottom: 5px;
}
.wares__item--choose .wares__price {
  margin-bottom: 10px;
}
.wares__item--choose .wares__price span {
  display: inline;
  font-family: "Neue-regular";
  color: #5F616D;
  font-size: 16px;
  line-height: 22px;
}

.wares__item--sale::before {
  content: "хит";
  position: absolute;
  top: 28px;
  left: 21px;
  width: 52px;
  height: 30px;
  font-size: 14px;
  line-height: 30px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  background: #22242A;
  border-radius: 6px;
}

.wares__fig {
  margin: 0;
  padding: 0;
}

.wares--sorting-tile .wares__fig {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  align-items: center;
}

.wares__item--choose .wares__fig {
  margin-top: 30px;
  align-items: end;
}

.wares--sorting-tile .wares__item--choose .wares__fig .wares__img-simple {
  display: none;
}

.wares--sorting-simple .wares__item--choose .wares__fig .wares__img-tile {
  display: none;
}

.wares--sorting-simple .wares__fig {
  width: 170px;
}

.wares__attributes {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 48px;
  height: 48px;
  z-index: 1000;
}

.wares--sorting-simple .wares__attributes {
  right: auto;
  top: 12px;
  left: 9px;
}

.wares--sorting-simple .wares__item--sale::before {
  top: auto;
  bottom: 53px;
  left: 12px;
}

.wares--sorting-simple .wares__sale {
  top: auto;
  bottom: 20px;
}

.wares--sorting-simple .wares__features-wrap {
  padding-top: 7px;
  padding-bottom: 20px;
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  padding-left: 33px;
  padding-right: 20px;
  border: 1px solid #EFEFEF;
  border-radius: 18px;
  align-items: flex-end;
}

.wares--sorting-simple .wares__features {
  width: 66%;
  margin-bottom: 8px;
}

.wares--sorting-simple .wares__order {
  padding-top: 25px;
  margin-bottom: 14px;
}

.wares--sorting-simple .wares__price {
  text-align: right;
  margin-bottom: 20px;
}

.wares--sorting-simple .wares__busket {
  margin-right: auto;
}

.wares__figcaption {
  padding-top: 26px;
  padding-bottom: 20px;
  margin-bottom: 20px;
  font-family: "Neue-semibold";
  font-size: 18px;
  line-height: 25.5px;
  letter-spacing: 0.05em;
  color: #22242A;
}

.wares--sorting-tile .wares__figcaption {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  height: 30px;
}

.wares--sorting-simple .wares__figcaption {
  padding-top: 16px;
  padding-bottom: 16px;
  margin-bottom: 5px;
}

.wares__item--choose .wares__figcaption {
  font-size: 24px;
  padding-left: 5px;
}

.wares--sorting-tile .wares__item--choose .wares__figcaption {
  padding-top: 31px;
}

.wares__features {
  margin-bottom: 28px;
}

.wares__feature {
  display: flex;
  justify-content: space-between;
  position: relative;
  margin-bottom: 5px;
}
.wares__feature span {
  background-color: #ffffff;
  z-index: 10;
  padding-right: 10px;
}
.wares__feature span:nth-child(2n) {
  padding-left: 10px;
  padding-right: 0;
}
.wares__feature::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 100%;
  height: 1px;
  background-color: #EEEEEE;
}

.wares__order {
  display: flex;
  align-items: center;
}

.wares--sorting-simple .wares__order {
  flex-direction: column-reverse;
}

.wares__item--choose .wares__order {
  display: block;
}

.wares__busket {
  color: #fff;
  text-align: center;
  background-color: #3F68FD;
  padding-top: 17px;
  padding-bottom: 17px;
  padding-left: 24px;
  padding-right: 24px;
  margin-right: 30px;
  border-radius: 0;
  font-family: "Neue-medium";
  font-size: 15px;
  line-height: 21px;
  border: none;
  border-radius: 12px;
  transition: 0.2s;
  cursor: pointer;
}
.wares__busket:hover, .wares__busket:active {
  background-color: #3056DE;
}

.wares__price {
  font-family: "Neue-bold";
  color: #3F68FD;
  font-size: 28px;
  line-height: 36.12px;
  letter-spacing: 0.05em;
}
.wares__price span {
  display: block;
}

.wares__old-price {
  font-family: "Neue-semibold";
  color: #9A9DAB;
  font-size: 14px;
  line-height: 19.6px;
  text-decoration: line-through;
  margin-bottom: 5px;
  letter-spacing: 0;
}

.wares__link {
  position: absolute;
  background-color: transparent;
}
.wares__link:hover {
  box-shadow: 0 0 15px rgba(51, 51, 51, 0.2);
  border-radius: 18px;
}

.wares--sorting-tile .wares__item--choose-wrap {
  background: linear-gradient(90deg, #F3F5F9 0%, #ECF1FC 100%);
  border-radius: 18px;
}

.wares--sorting-tile .wares__item--choose-add {
  display: none;
}

.favorite {
  position: relative;
  padding: 0;
  border: 0;
  font: inherit;
  color: inherit;
  cursor: pointer;
  position: absolute;
  z-index: 10;
  width: 48px;
  height: 48px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  display: inline-block;
  vertical-align: middle;
}
.favorite::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 24px;
  height: 24px;
  background-image: url("../img/favorite.svg");
  transform: translate(-50%, -50%);
}

.favorite--active::before {
  background-image: url("../img/favorite-active.svg");
}